.padding {
  width: 1140px;
  margin: 0 auto;
}

.Modules__Section {
  background: #F1F1F1;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 14px;
  width: 80%;
  margin: 60px auto;
  padding: 10px;
  cursor: pointer;
  transition: all 0.4s ease;
}

.Modules__Section:hover {
  -webkit-filter: brightness(95%);
          filter: brightness(95%);
}

.Modules__Section .content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  text-align: left;
  padding: 0px 20px 0px 60px;

  transition: all 0.4s ease;
}

.Modules__Section .content ol ul {
  margin: 10px 0px 40px 10px;
}

.Modules__Section .content ol ul li {
  margin: 10px;
}

.Modules__Section .container-text {
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
}

.Modules__Section .container-text svg {
  position: absolute;
  right: 0px;

  transition: transform 0.4s ease;
}

.Modules__Section.open .content {
  max-height: 3000px;
  opacity: 1;
  padding-top: 50px;
}

.Modules__Section.open .container-text svg {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}

.faq {
  width: 100%;
  position: relative;
}

.faq .faq-question {
  font-size: 20px;
  font-weight: 600;
  position: relative;
  cursor: pointer;
  transition: all 0.4s ease;
  padding-right: 20px;
}

.faq .faq-question:hover {
  color: var(--secondary-color);
}

.faq:hover svg {
  color: var(--secondary-color);
}

.faq .faq-answer {
  font-size: 16px;
  padding: 10px 20px;

  opacity: 0;
  max-height: 0;
  overflow-y: hidden;
  transition: all 0.5s ease;
}

.faq svg {
  position: absolute;
  top: 0;
  right: 0;
  transition: all 0.4s ease;
}

.faq.open svg {
  transform: rotate(180deg);
}

.faq.open .faq-question {
  margin-bottom: 10px;
}

.faq.open .faq-answer {
  max-height: 1000px;
  opacity: 1;
  margin-bottom: 10px;
}

